/*landing-work*/
.landing-work{
	padding-left: 0;
	padding-right: 0;
}

.landing-work-title{
	text-align: center;
}

.landing-work-item-preview{
    transform: translateY(-50%);
    position: absolute;
    left: 0;
    top: 50%;
    width: 36%;
    padding-left: 1rem;
    padding-right: 1.5rem;
    text-align: center;
}

.landing-work-item-preview>svg{
	float:right;
	max-width: 6rem;
	max-height: 6rem;
	fill:white;
}

.landing-work-item-content{
	transform: translateY(-50%);
	position: absolute;
	left: 36%;
	top:50%;
	width: 64%;
	padding-right: 1rem;
}

.landing-work-item{
	transition: box-shadow 0.125s;
	overflow: hidden;
	position: relative;
	margin: 0 auto;
	max-width: 45rem;
	height:7.5rem;
	color: white;
	box-shadow: inset 0 0 0 0 white;
}

.landing-work-item:hover{
	box-shadow: inset 0 0 0 0.375rem #fff;
}

.landing-work-item-title{
	margin-bottom: 0;
	font-size: 1.3125rem;
	font-weight: 500;
	color: inherit;
}

.landing-work-item-subtitle{
	margin-bottom: 0;
	font-size: 1rem;
	font-weight: 300;
	color: inherit;
}

.landing-work-item-anchor{
	position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.landing-work-item-dragon{
	background-color: #008080;
}

.landing-work-item-lampno6{
	background-color: #b69570;
}

.landing-work-item-visier{
	background-color: #5988ff;
}

.landing-work-item-wayfind{
	background-color: #d60036;
}

.landing-work-item-breathless{
	background-color: #000;
}
